body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{text-align:center}.mobileMessage{display:none}body{background-color:#f2f2f2}*{font-size:14px;line-height:1.4}p{-webkit-margin-before:0;margin-block-start:0;-webkit-margin-after:0;margin-block-end:0}@media only screen and (max-width: 600px){.App{display:none}.mobileMessage{display:block}.mobileMessage h1,.mobileMessage p{text-align:center}}

.textEditor{max-width:720px;margin:0 auto 20px;padding:20px}.textEditor .buttons{padding:1px 18px 17px;margin:0px -20px 20px;border-bottom:2px solid #eee;text-align:start}.textEditor .buttons>button{text-align:center;width:18px;height:18px}.textEditor .buttons>button:last-child{width:23px;height:20px}.textEditor .editable{text-align:start;outline:none;white-space:pre-wrap;overflow-wrap:break-word}.textEditor,.buttons,.editable{background-color:white}

button{font-size:18px;vertical-align:text-bottom;all:unset;cursor:pointer}button:not(:first-child){margin-left:15px}button .active{color:black}button .inactive{color:#f2f2f2}

